			if(showIcon == true){
				html += "<div class='ug-thumb-icon' style='background-image:url(\""+objItem.urlThumb+"\")'></div>";
				html += "<div class='ug-thumb-right'>";
			}
				
			html += "<div class='ug-thumb-title'>" + objItem.title + "</div>";
			
